import React, { Component } from 'react';
import {connect} from 'react-redux';
import { actionCreators as homeActions, selector } from '../';
import { bindActionCreators } from 'redux';
import { Divider } from "@blueprintjs/core";

import {
    Link
} from "react-router-dom";

function Home() {
    return <>
        <h3>Design and Development support for Nottingham businesses and charities</h3>
        
        <div style={{marginTop:10, lineHeight:1.7}}>
            <p>We are a group of IT professionals; <strong>designers, web and app developers and IT managers </strong>who want to do our bit to support the Nottingham business, charity and voluntary sectors during the Covid-19 pandemic. </p> 
            
            <p> We expect that there are many small businesses, charities and volunteer organisations who are having a tough time and who need develop their online resources to better deal with this crisis. We would very much like to offer some <strong>free</strong> help.  Whatever you're grappling with technically; whether creating a new skeleton service, getting your stock online, creating shopping carts and payment gateways, supporting order fulfilment, becoming more efficient or simply wanting a little advice - we may be able to help.  If you're interested, please fill in a few details on the <Link to="/apply">apply</Link> page and we will get back to you as soon as we can.</p>

            <h4>How it works</h4>
            <ol>
               <li><Link to="/apply">Apply</Link> for some help, giving us details of what you need </li>
               <li>We will review it, and try to find a volunteer able to take it on</li>
               <li>We will spend as much time as we can trying to give you a hand</li>
            </ol>

            <h4> Please volunteer! </h4>
            <p>If you have technical skills, whether in coding or setting up and managing IT solutions, we could do with your help!  Please provide your details on the <Link to="/volunteer">volunteer</Link> page, where we provide further information on the skills that we require.  We are finding our way at the moment, trying to understand the limits of what we might achieve and how we might best support the local business, charity and voluntary sectors.  If you have any ideas please do get in touch. </p>
        </div>
       
    </>
}

export default connect(selector, (dispatch) => ({
    actions: bindActionCreators(homeActions, dispatch)
}))(Home);